/* eslint-disable no-undef */
ymaps.ready(() => {
  const myMap = new ymaps.Map('map', { //addControl(control, controlPosition) !!!!!!!!!!!!! ---- disableScrollZoom()
    center: [55.751574, 37.573856],
    zoom: 9,
    controls: [],
  }, {
    searchControlProvider: 'yandex#search',
  });

  const myPlacemark = new ymaps.Placemark(myMap.getCenter(), {
    hintContent: 'Значок метки',
  }, {
    // Опции.
    // Необходимо указать данный тип макета.
    iconLayout: 'default#image',
    // Своё изображение иконки метки.
    iconImageHref: 'build/images/pin.png',
    // Размеры метки.
    iconImageSize: [34, 40],
    // Смещение левого верхнего угла иконки относительно
    // её "ножки" (точки привязки).
    iconImageOffset: [-5, -38],
  });

  myMap.geoObjects.add(myPlacemark);

  const objectManager = new ymaps.ObjectManager({
    clusterize: true,
    gridSize: 32,
    clusterDisableClickZoom: true,
  });

  // Чтобы задать опции одиночным объектам и кластерам,
  // обратимся к дочерним коллекциям ObjectManager.
  objectManager.objects.options.set({
    iconLayout: 'default#image',
    iconImageHref: 'build/images/pin.png',
    iconImageSize: [34, 40],
});
  objectManager.clusters.options.set('preset', 'islands#invertedBlueClusterIcons');
  myMap.geoObjects.add(objectManager);

  $.ajax({
    url: '/assets.json',
  }).done((data) => {
    objectManager.add(data);
  });
});
